// Libs
import React from 'react'

const validateEmail = (values, errors) => {
  if (values.email && !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) { 
    errors.email = ' email'
  }
}


export const validate = resource => values => {
  const errors = {}
  const errorStyle = {
    color: '#f44336', 
    fontSize: '0.75rem', 
    fontFamily: 'Roboto, Helvetica, Arial, sans-serif'
  }



  switch (resource) {      
    case 'realty':     
      Object.keys(values).forEach(field => {
        if (!values[field] && (field === 'description_ru' || field === 'description_en')) {
          errors[field] = eval(<span style={errorStyle}>   !</span>);
        } 
        else if (!values[field]) {
          errors[field] = ' '
        }
      }) 
      if (String(values.booking_mark)[1] !== '.' || String(values.booking_mark).length > 3) {
        errors.booking_mark = "1     1   : 9.3  7.0.  - ,   ."
      }        
      return errors

    case 'feedback':  
      Object.keys(values).forEach(field => {
        if (!values[field]) {
          errors[field] = eval(<span style={errorStyle}> !</span>)
        }
      }) 
      return errors

    case 'content':  
      Object.keys(values).forEach(field => {
        if (!values[field]) {
          errors[field] = eval(<span style={errorStyle}> !</span>)
        }
      }) 
      return errors

    default: 
      return errors
  }
}